<template>
  <div class="mysecure">
    <van-nav-bar title="安全中心" left-text="返回" left-arrow right-text="退出登录" @click-left="goback" />

    <div class="content">
      <van-cell-group>
        <van-cell title="修改密码" is-link icon="youzan-shield" @click="showPopup" />
        <van-cell title="注销账号" is-link icon="youzan-shield" />
      </van-cell-group>
    </div>
    <div class="exit">
      <van-button type="warning" @click="logout" block>退出登录</van-button>
    </div>
    <!-- popup 及 修改密码form -->
    <van-popup v-model="isShow" position="bottom" closeable :style="{height:'30%'}">
      <van-form @submit="onSubmit">
        <div class="pop">
          <van-field
            v-model="pwd.oldpassword"
            name="旧密码"
            label="旧密码"
            placeholder="旧密码"
            :rules="[{ required: true, message: '请填写旧密码' }]"
          />
          <van-field
            v-model="pwd.newpassword"
            type="text"
            name="新密码"
            label="新密码"
            placeholder="新密码"
            :rules="[{ required: true, message: '请填写新密码' }]"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      pwd: {
        oldpassword: "",
        newpassword: ""
      }
    };
  },
  created() {},
  methods: {
    goback() {
      this.$router.push({ name: "My" });
    },
    showPopup() {
      this.isShow = true;
    },
    onSubmit(values) {
      //提交
      
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids) //截取 create的sids
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 50000) {
            result.data.result.forEach(v => {
              this.counts += v.count;
              this.total += v.price * v.count;
            });
          }
          this.orderProducts = result.data.result;
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //退出登录
    logout() {
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "是否确认退出登录！",
          confirmButtonColor: "#0C34BA",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          localStorage.removeItem("token");

          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 1000);
        });
    },
  }
};
</script>

<style lang="less" scoped>
.mysecure {
  width: 100%;
  .content {
    width: 100%;
    margin: 20px auto;
  }
  .pop {
    padding: 10px 20px;
  }
}
</style>